/**
 * Obtains the URL for opening documents in the Pega Infinity portal.
 * @function
 * @private
 * @static
 * @example <caption>In this example, the API get the URL for documents.</caption>
 * PCore.getAttachmentUtils().getDocumentURL(getPConnect())
 * .then(() => {
 *    // success
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param pConnectObj   The pConnect object of the component from where the API is being called.
 * @returns A Promise that returns the URL for documents.
 */
const getDocumentURL = (pConnectObj: C11nEnv) => {
  const dataViewName = PCore.getEnvironmentInfo().getDocumentUrlDP() || 'D_pxGetDocumentURL';
  return PCore.getDataPageUtils()
    .getPageDataAsync(dataViewName, pConnectObj.getContextName(), {}, { invalidateCache: true })
    .catch((error: { response: string }) => {
      pConnectObj.reportError(': Error occurred during ajax call getDocumentURL API : ', error.response);
    });
};

const getIconFromFileType = (fileType: string) => {
  let icon = 'document-doc';
  if (!fileType) {
    return icon;
  }
  if (fileType.startsWith('audio')) {
    icon = 'audio';
  } else if (fileType.startsWith('video')) {
    icon = 'video';
  } else if (fileType.startsWith('image')) {
    icon = 'picture';
  } else if (fileType.includes('pdf')) {
    icon = 'document-pdf';
  } else {
    const [, subtype] = fileType.split('/');
    const foundMatch = (sources: string[]) => sources.some((key: string) => subtype.includes(key));

    if (foundMatch(['excel', 'spreadsheet'])) {
      icon = 'document-xls';
    } else if (foundMatch(['zip', 'compressed', 'gzip', 'rar', 'tar'])) {
      icon = 'document-compress';
    }
  }

  return icon;
};

/**
 * Transform attachment response properties.
 *
 * @function
 * @private
 * @static
 * @example <caption>In this example, the API transforms the attachment response properties into new response structure.</caption>
 * PCore.getAttachmentUtils().buildFilePropsFromResponse(FileResponseObject)
 * .then(() => {
 *    // success
 * }).catch(err => {
 *   // errors
 * });
 *
 * @param respObj The FileResponseObject object for which mapping need to be done.
 * @returns A new FileResponseObject would be returned.
 */
// eslint-disable-next-line sonarjs/cognitive-complexity
const buildFilePropsFromResponse = (respObj: FileResponseObject) => {
  // introduced as part of BUG-849659 :
  // pyCategoryLabel : Stores the pyLabel of the attachment category rule from class "Rule-Obj-AttachmentCategory"
  // pxCreateOpName: Full name of the operator who created the attachment.
  let categoryName;
  if (respObj.pyCategoryLabel) {
    categoryName = `${respObj.pyCategoryLabel},`;
  } else {
    categoryName = respObj.pyCategoryName ? `${respObj.pyCategoryName},` : respObj.Category;
  }
  let createOperatorName;
  if (respObj.pxCreateOpName) {
    createOperatorName = respObj.pxCreateOpName;
  } else {
    createOperatorName = respObj.pxCreateOperator ? respObj.pxCreateOperator : respObj.CreateUser?.Name || '';
  }
  // infinity Example: { "pyFileName": "filename", "pyAttachName":"filename.png" }
  // extension value doesn't come separately in infinity
  // LP example: { "FileName": "filename.png", "FileExtension": "png" }
  let fileName;
  if (respObj.pyFileName) {
    fileName = respObj.pyFileName;
  } else {
    fileName =
      respObj.FileExtension && respObj.FileExtension !== '' && respObj.FileName.endsWith(respObj.FileExtension)
        ? respObj.FileName.substring(0, respObj.FileName.lastIndexOf(respObj.FileExtension) - 1)
        : respObj.FileName;
  }
  let fileExtension;
  if (respObj.pyAttachName && respObj.pyFileName) {
    fileExtension =
      respObj.pyAttachName.startsWith(respObj.pyFileName) &&
      respObj.pyAttachName.split('.').length - 1 === respObj.pyFileName.split('.').length
        ? respObj.pyAttachName.split('.').pop()
        : '';
  } else {
    fileExtension = respObj.FileExtension;
  }

  return {
    props: {
      meta: `${categoryName} ${createOperatorName}`,
      name: respObj.pyAttachName ? respObj.pyAttachName : respObj.FileName,
      icon: getIconFromFileType(respObj.pyMimeFileExtension || respObj.MimeType),
      error: respObj.error || null,
      thumbnail: respObj.thumbnail || null
    },
    responseProps: {
      fileName: respObj.pyAttachName ? respObj.pyAttachName : respObj.FileName,
      name: fileName,
      category: respObj.pyCategoryName ? respObj.pyCategoryName : respObj.Category,
      extension: fileExtension,
      ID: respObj.pzInsKey ? respObj.pzInsKey : respObj.ID,
      mimeType: respObj.pyMimeFileExtension ? respObj.pyMimeFileExtension : respObj.MimeType,
      attachmentURL: respObj.pyAttachmentLink ? respObj.pyAttachmentLink : respObj.AttachmentURL,
      createUser: createOperatorName,
      createDateTime: respObj.pxCreateDateTime ? respObj.pxCreateDateTime : respObj.CreateDateTime,
      responseType: respObj.responseType ?? (respObj.ID ? 'binary' : null),
      localAttachment: !!respObj.localAttachment
    }
  };
};

/**
 * Prepare attachment data compatible with attachment DX Component
 *
 * @function
 * @private
 * @static
 * @example <caption>In this example, the API transforms the attachment response into predefined object structure.</caption>
 * const { hasUploadedFiles, attachments, categoryName } = PCore.getAttachmentUtils().prepareAttachmentData(attachmentResponse)
 *
 * @param attachmentResponse   The attachmentResponse object which gets modified into another Attachment DX Component understandable object.
 * @returns A new attachment object would be returned.
 */
const prepareAttachmentData = (attachmentResponse: AttachmentObject): AttachmentData => {
  let attachments: AttachmentProperties[] = [];
  let isOldAttachment = false;
  if (attachmentResponse && Object.hasOwn(attachmentResponse, 'pyCount')) {
    isOldAttachment = true;
    attachments =
      attachmentResponse?.pxResults
        ?.filter((file: FileResponseObject) => !!file.pzInsKey)
        .map((file: FileResponseObject) => buildFilePropsFromResponse(file)) || [];
  } else if (attachmentResponse && typeof attachmentResponse === 'object') {
    let attachmentList;
    if (Array.isArray(attachmentResponse)) {
      attachmentList = attachmentResponse;
    } else {
      attachmentList = [attachmentResponse];
    }

    attachments = attachmentList
      ?.filter((file: FileResponseObject) => (file.ID && file.FileName) || file.pzInsKey)
      .map(buildFilePropsFromResponse);
  } else {
    attachments = [];
  }

  return {
    hasUploadedFiles: attachments.length > 0,
    categoryName: attachmentResponse?.pyCategoryName || attachments?.[0]?.responseProps?.category || '',
    attachments,
    isOldAttachment
  };
};

/**
 * Validate attachment data for required validation
 *
 * @function
 * @private
 * @static
 * @param value The attachment redux object.
 * @param contextTreeNodeValue context tree object
 * @returns Empty object or redux value will be returned.
 */
const validateAttachments = (value: AttachmentValidate, contextTreeNodeValue: AttachmentData | AttachmentObject) => {
  let newValue: string | AttachmentValidate = value;
  if (typeof contextTreeNodeValue === 'object' && 'attachments' in contextTreeNodeValue) {
    const { attachments }: { attachments: AttachmentProperties[] } = contextTreeNodeValue as AttachmentData;

    if (attachments.length === 0) {
      newValue = '';
    }
  } else {
    // Maintain backward compatibility for old attachments
    const { attachments }: { attachments: AttachmentProperties[] } = prepareAttachmentData(
      contextTreeNodeValue as AttachmentObject
    );
    if (attachments.length === 0) {
      newValue = '';
    }
  }
  return newValue;
};

export default {
  uploadAttachment,
  linkAttachmentsToCase,
  getCaseAttachments,
  getAttachmentCategories,
  downloadAttachment,
  editAttachment,
  deleteAttachment,
  downloadDocument,
  deleteDocument,
  getDocumentURL,
  cancelRequest,
  buildFilePropsFromResponse,
  getAttachmentURL,
  prepareAttachmentData,
  validateAttachments
};
