class FetchStatus {
  private updateSessionInProgress: boolean;

  constructor() {
    // conveys re-auth in progress for all auth types
    this.updateSessionInProgress = false;
  }

  getUpdateSessionStatus = () => {
    return this.updateSessionInProgress;
  };

  setUpdateSessionStatus = (val: boolean) => {
    this.updateSessionInProgress = val;
  };
}

export default FetchStatus;
