/**
 * Provides APIs to update and retrieve metadata from the store
 * @module RulesStore
 */

const mergeNewRules = (
  rule: string,
  store: { [key: string]: StoreType },
  storeRuleModels: StoreType,
  ruleModels: StoreType
) => {
  const tempStoreModels = Array.isArray(storeRuleModels) ? storeRuleModels : [storeRuleModels];
  const tempRuleModels = Array.isArray(ruleModels) ? ruleModels : [ruleModels];

  tempRuleModels.forEach((newRule) => {
    const index = tempStoreModels.findIndex((storeRule) => storeRule.classID === newRule.classID);
    if (index === -1) {
      tempStoreModels.push(newRule);
    } else {
      const defaultProps = { isDeclarativeTarget: false };
      const newMergedRule = { ...tempStoreModels[index], ...defaultProps, ...newRule };
      tempStoreModels[index] = newMergedRule;
    }
  });

  store[rule] = tempStoreModels.length === 1 ? tempStoreModels[0] : tempStoreModels;
};

/**
 * [mergeStore]
 * Description : merges the store with the source rules based on the classID. If a rule with the same name and classID
 * exists it will be merged, else created.
 * @function mergeStore
 * @param rules   source rules
 * @param store   Existing store
 * @static
 * @private
 * @example <caption>Example for mergeStore.</caption>
 * PCore.getMetadataUtils().mergeStore({ apartment: { classID: "c-1" }}, {})
 * example for merging the apartment field rule into the store
 */
export const mergeStore = (rules: ResourceType = {}, store: { [key: string]: StoreType } = {}) => {
  // Blind copy to store if store is empty
  if (Object.keys(store).length === 0) {
    Utils.copy(rules, store);
    return;
  }

  Object.keys(rules).forEach((rule) => {
    const ruleModels = rules[rule];
    const storeRuleModels = store[rule];
    if (!storeRuleModels) {
      // store has no entry then set RuleModel
      store[rule] = ruleModels;
    } else {
      // merging new rules with the rules in the store
      mergeNewRules(rule, store, storeRuleModels, ruleModels);
    }
  });
};

/**
 * returns property metadata from store
 * @function getPropertyMetadata
 * @param {string|undefined} propertyName property name like 'FirstName' whose metadata must be obtained
 * @param {string} currentClassID (optional) class id to which property belongs to
 * @static
 * @returns property metadata as an object from store. If property name is not passed or property metadata
 * or store doesn't exists then null is returned
 * @example <caption>In this example, the API obtains the metadata of the apartment property under the c-1 class.</caption>
 * PCore.getMetadataUtils().getPropertyMetadata('apartment', 'c-1')
 * {
 *     classID: 'c-1',
 *     type: 'text'
 * }
 */
export const getPropertyMetadata = (
  propertyName: string | undefined,
  currentClassID: string = ''
): PropertyModel | PropertyModel[] | null => {
  if (!propertyName) {
    return null;
  }

  const propertyModels = propertyModelStore[propertyName];

  if (!propertyModels) {
    return null;
  }

  if (!currentClassID) {
    return Utils.deepClone(propertyModels);
  }

  let metadata;
  if (Array.isArray(propertyModels)) {
    metadata = propertyModels.find((modal) => modal.classID.toLowerCase() === currentClassID.toLowerCase()) || null;
  } else {
    metadata =
      propertyModels.classID.toLowerCase() === currentClassID.toLowerCase() ||
      ((propertyModels.type === 'Page' || propertyModels.type === 'Page List') &&
        propertyModels?.pageClass?.toLowerCase() === currentClassID.toLowerCase())
        ? propertyModels
        : null;
  }
  return metadata ? Utils.deepClone(metadata) : null;
};

export const getLocaleUtilsForLanguage = (locale: string) => {
  if (!languagePackStore[locale]) {
    languagePackStore[locale] = new LocaleUtils();
  }
  return languagePackStore[locale];
};

/**
 *
 * @param reference unique identifier of the a LocaleFields in Languagepack store
 * @param callback to be called to create LocalizationService for the given reference
 * @returns LocalizationService instance for the given reference
 *
 * @example <caption>In this example, the API obtains the LocalizationService for the reference "C-1!PAGE!VIEW1".</caption>
 * getLocalizationService("C-1!PAGE!VIEW1", () => {
 *  return new LocalizationService("C-1!PAGE!VIEW1");
 * });
 * returns LocalizationService instance for the given reference
 * @private
 */
export const getLocalizationServiceFromStore = (reference: string, callback: () => LocalizationService) => {
  if (!localizationServiceStore[reference]) {
    localizationServiceStore[reference] = callback();
  }
  return localizationServiceStore[reference];
};

/**
 * returns embedded property's leafNode metadata from store
 * @function getEmbeddedPropertyMetadata
 * @param {string} propertyName property name like 'Customer.Address.City' whose metadata must be obtained
 * @param {string} currentClassID class id to which top level property belongs to
 * @param {string} embeddedType embedded field type to which current leafNode belongs to
 * @param {string[]} categoryPath (optional) category to which the current leafNode belongs to.
 * @static
 * @returns embedded property's leafNode metadata as an object from store. If currentClassID is not passed
 * or property metadata or store doesn't exists then null is returned
 *
 * @example <caption>In this example, the API obtains the metadata of the City leaf node, which needs to be resolved from the c-1 top level class.</caption>
 *
 * //{Customer: [{ classID: 'c-1', pageClass: 'c-2', label:'Customer' }],
 * // Address: [{ classID: 'c-2', pageClass: 'c-3', label:'Address' }],
 * // City: [{ classID: 'c-3', label:'City' }]}
 *
 * PCore.getMetadataUtils().getEmbeddedPropertyMetadata('Customer.Address.City', 'c-1')
 * returns
 * {
 *    classID: 'c-3',
 *    category: 'Customer.Address'
 * }
 */

export const getEmbeddedPropertyMetadata = (
  propertyName: string,
  currentClassID?: string,
  embeddedType?: string,
  categoryPath: string[] = []
): object | null => {
  if (!currentClassID || !propertyName) {
    return null;
  }
  if (!propertyName.includes('.')) {
    let category;
    const metadata = getPropertyMetadata(propertyName, currentClassID);
    if (categoryPath.length) {
      category = categoryPath.join('.');
    }
    return {
      ...metadata,
      embeddedType,
      category
    };
  }
  const [first, ...rest] = propertyName.split('.');
  const metadata = getPropertyMetadata(first, currentClassID) as PropertyModel;
  if (metadata) {
    categoryPath.push(metadata.label as string);
  }
  return getEmbeddedPropertyMetadata(rest.join('.'), metadata?.pageClass, metadata?.type, categoryPath);
};

/**
 * This API updates the view store
 * @function updateViewStore
 * @param views the object of the views
 * @static
 * @private
 * @example <caption>Example for updateViewStore.</caption>
 * PCore.getMetadataUtils().updateViewStore({
 *  "view1": { a: 'a' }
 *});
 * example for updating view store with View1
 */
export const updateViewStore = (views: { [key: string]: ViewStore }) => {
  mergeStore(views, viewStore);
};

/**
 * This API updates the navigation
 * @function updateNavigationStore
 * @param navigations the object of navigation rules
 * @static
 * @private
 * @example <caption>Example for updateNavigationStore.</caption>
 * PCore.getMetadataUtils().updateNavigationStore({
 *      Navigation1: {
 *          nav: "nav"
 *      }
 *});
 * example for updating navigation store with Navigation1
 */
export const updateNavigationStore = (navigations: { [key: string]: NavigationStore }) => {
  mergeStore(navigations, navigationStore);
};

const mergeByResource = (resources: Resources) => {
  if (resources.views) mergeStore(resources.views, viewStore);
  if (resources.navigations) mergeStore(resources.navigations, navigationStore);
  if (resources.personalizations) mergeStore(resources.personalizations, personalizationStore);
  if (resources.insights) mergeStore(resources.insights, insightStore);
  if (resources.paragraphs) mergeStore(resources.paragraphs, paragraphStore);
  if (resources.dataTypes) mergeStore(resources.dataTypes, dataTypeStore);
  if (resources.datapages) mergeStore(resources.datapages, dataPagesStore);
  if (resources.genAICoaches) {
    mergeStore(resources.genAICoaches, genAICoachStore);
  }
  if (resources.AIAgents) {
    mergeStore(resources.AIAgents, AIAgentStore);
  }
};

const mergeFields = (resources: Resources) => {
  const fields = resources.fields || {};

  Object.keys(fields).forEach((key: string) => {
    const fieldArray = fields[key];

    if (Array.isArray(fieldArray)) {
      fieldArray.forEach((fieldObj) => {
        if (resources.datapages && fieldObj.datasource && fieldObj.datasource.tableType === 'DataPage') {
          fieldObj.datasource.mode = resources.datapages?.[fieldObj.datasource.name].mode;
        }
        // generate localeReference
        const localeReference = getLocaleReference(
          LocaleRuleTypes.FIELD,
          key,
          fieldObj.definedOnClassID ?? fieldObj.classID
        );
        fieldObj.localeReference = localeReference;
      });
      // if only single entry present for field then return object.
      if (fieldArray.length === 1) {
        const [objData] = fieldArray;
        fields[key] = objData;
      }
    } else if (resources.datapages && fieldArray?.datasource && fieldArray.datasource.tableType === 'DataPage') {
      // TODO: In future we will always get array once everything is stable remove this code.

      fieldArray.datasource.mode = resources.datapages[fieldArray.datasource.name].mode;
    }
  });
};

/**
 * Update store with given resources
 * @function updateStores
 * @param resources uiMetadata - meta data information
 * @static
 * @private
 * @example <caption>Example for updateStores.</caption>
 * PCore.getMetadataUtils().updateStores({
 *   resources: {
 *     personalizations: {
 *        "test1234": {}
 *  }
 *  }
 *});
 * example for updating store with personalizations
 */
export const updateStores = (resources: Resources) => {
  mergeByResource(resources);
  mergeFields(resources);

  const { datapages = {} } = resources;
  Object.keys(datapages).forEach((key) => {
    dataPageModelStore[key] = datapages[key] as unknown as DataPageModel;
  });

  mergeStore(resources.fields, propertyModelStore);
};

