/**
 * determines if view generated for a classID exists in store
 *
 * @function isViewExists
 * @param {string} viewName - view name whose existence needs to be confirmed
 * @param {string} classID - class ID for which view was generated
 * @static
 * @returns {boolean} true if view generated for a classID exists in store
 *
 * @example <caption>In this example, the API returns the Boolean value true if the view View1 generated for the class O02LDN-CosmoReact-Work-Test exists in the Store.</caption>
 * PCore.getMetadataUtils().isViewExists("View1", "O02LDN-CosmoReact-Work-Test");
 */
export const isViewExists = (viewName: string, classID: string) => {
  const view = viewStore[viewName];
  if (Array.isArray(view)) {
    return view.some((prop) => prop.classID === classID);
  }
  return view?.classID === classID;
};

