/**
 * This API returns fields of given class name
 *
 * @function getFieldsByClassName
 * @param className name of the class
 * @static
 * @returns object of the fields for the given class name
 * @private
 * @example <caption>Example for getFieldsByClassName.</caption>
 * PCore.getMetadataUtils().getFieldsByClassName("O02LDN-CosmoReact-Work-Test ");
 * example for getting fields of the "O02LDN-CosmoReact-Work-Test" class
 */
export const getFieldsByClassName = (className: string): { [key: string]: Field } => {
  const fields: { [key: string]: Field } = {};
  Object.keys(propertyModelStore).forEach((fieldName) => {
    const props = propertyModelStore[fieldName];
    let matchedField;
    if (Array.isArray(props)) {
      matchedField = props.find((prop) => className === prop.classID);
    } else if (className === props.classID) {
      matchedField = props;
    }
    if (matchedField) {
      fields[fieldName] = Utils.deepClone(matchedField);
    }
  });
  return fields;
};

/**
 * check if a property has a datasource and returns its parameters
 *
 * @function getFieldParameters
 * @param {string} propertyName name of the property whose parameters must be obtained
 * @param {string} classID classID of the property
 * @static
 * @returns property parameters as an object. If property data source doesn't has parameters or property is not associated to datasource
 * null be returned
 * @example <caption>In this example, the API obtains the parameters of the property TestProperty that belongs to the class c-1.</caption>
 * PCore.getMetadataUtils().getFieldParameters("TestProperty", "c-1");
 * returns
 *     {
 *        "param1":"test"
 *     }
 */
export const getFieldParameters = (propertyName: string, classID: string) => {
  const meta = getPropertyMetadata(propertyName, classID) as PropertyModel;
  const params = meta?.datasource?.parameters;
  if (params) {
    return Utils.deepClone(params);
  }

  return null;
};

/**
 * This API returns paragraph of given class name
 *
 * @function getParagraph
 * @param className name of the paragraph
 * @static
 * @returns object of the paragraph for the given class name
 * @private
 * @example <caption>Example for getParagraph.</caption>
 * PCore.getMetadataUtils().getParagraph("testPara", " O02LDN-CosmoReact-Work-Test ");
 * example for getting paragraph of the "O02LDN-CosmoReact-Work-Test" class
 */
export const getParagraph = (paragraphName: string, classID: string) => {
  let value: ParagraphStore | null = null;
  const paragraphObj = paragraphStore[paragraphName];

  if (paragraphObj) {
    if (Array.isArray(paragraphObj)) {
      value = paragraphObj.find((paragraph) => paragraph.classID === classID);
    } else if (paragraphObj.classID === classID) {
      value = paragraphObj;
    }
  }

  return value ? Utils.deepClone(value) : null;
};

