/**
 * returns keys of the data page
 *
 * @function getDataPageKeys
 * @param {string} dataPageName data page name whose keys must be obtained
 * @static
 * @returns keys as an array of objects containing (linkedField, keyName and isAlternateKeyStorage). If data page doesn't exists null will be returned or
 * if data page class doesn't exists in store it will return empty
 * @private
 * @example <caption>example of getting data page keys for "TestDataPage"</caption>
 * PCore.getMetadataUtils().getDataPageKeys("TestDataPage");
 * returns
 *    [{
 *       "linkedField":"RegNo",
 *       "keyName":"pyGUID",
 *       "isAlternateKeyStorage":true
 *    }]
 */
export const getDataPageKeys = (dataPageName: string): DataPageObj[] | null => {
  const dataPage = dataPagesStore[dataPageName];
  if (!dataPage) {
    return null;
  }

  const { classID: dataClass, isAlternateKeyStorage, parameters } = dataPage;

  if (isAlternateKeyStorage) {
    return parameters.map((item) => ({
      keyName: item.name,
      isAlternateKeyStorage,
      linkedField: item.linkedField
    }));
  }

  const classData = dataTypeStore[dataClass];
  if (!classData) {
    return [];
  }

  return classData.classKeys.map((keyName: string) => ({
    keyName,
    isAlternateKeyStorage
  }));
};

