/**
 * returns lookup datapage info of a data class
 *
 * @function getLookUpDataPageInfo
 * @param {string} dataClass unique identifier of a data class whose lookup data page info must be obtained
 * @static
 * @returns lookup data page info as an object. if lookup data page  doesn't exists in store  null will be returned
 * @private
 * @example <caption>example of getting getLookUpDataPageInfo for "Registration" classID</caption>
 * // data type
 * //  {
 * //     "Registration":{
 * //        "savableDataPage":"D_RegistrationSavable",
 * //        "lookupDataPage":"D_Registration",
 * //        "classKeys":[
 * //           "pyGUID"
 * //         ],
 * //        "lookupDataPageInfo":{
 * //           "isAlternateKeyStorage":true,
 * //          "parameters":{
 * //              "TestID":"@P .TestID"
 * //           }
 * //        }
 * //     }
 * //  }
 * PCore.getMetadataUtils().getLookUpDataPageInfo("Registration");
 * returns
 *    {
 *       "isAlternateKeyStorage":true,
 *       "parameters":{
 *          "TestID":"@P .TestID"
 *       }
 *    }
 */
export const getLookUpDataPageInfo = (dataClass: string) => {
  if (Object.hasOwn(dataTypeStore, dataClass)) {
    const { lookupDataPageInfo } = dataTypeStore[dataClass];
    return lookupDataPageInfo;
  }
  return null;
};

/**
 *
 * @param propertyReference
 * @param context
 * @private
 */
export const getAdditionalInfo = (propertyReference: string, context: string) => {
  const pageRef = propertyReference.split('.');
  const propName = pageRef.pop();
  const c11nEnv = new C11nEnv({
    options: {
      pageReference: `${pageRef.join('.')}`,
      context
    }
  });

  const additionalInfo = (getPropertyMetadata(propName, c11nEnv.getCurrentClassID()) as PropertyModel)
    ?.additionalInformation;

  if (!additionalInfo?.includes('@PARAGRAPH')) {
    return additionalInfo;
  }

  const paragraphRuleName = AnnotationProcessor.trimAnnotation(additionalInfo);

  return ParagraphUtils.processParagraph(paragraphRuleName, c11nEnv);
};

/**
 * returns the resolved fieldMetadata of the given property
 * @param {string} propertyReference The complete reference of the property whose metadata has to be obtained
 * @param {string} context The context in which the prop is present
 * @returns resolved fieldMetadata as an object
 * @example <caption>In this example, the API obtains the resolved field metadata of the FirstName property under the app/primary context.</caption>
 * //fieldMetadata
 * //   {
 * //      "FirstName":[
 * //         {
 * //            "classID":"Address",
 * //            "type":"text",
 * //            "maxLength":10,
 * //            "additionalInformation":"@PARAGRAPH Instruction"
 * //         }
 * //      ]
 * //   }
 * const propertyRef = "caseInfo.content.FirstName";
 * const context = "app/primary";
 * const resolvedFieldMetadata = PCore.getMetadataUtils().getResolvedFieldMetadata(propertyReference, context);
 * returns
 *    {
 *       "classID":"Address",
 *       "type":"text",
 *       "maxLength":10,
 *       "additionalInformation":"<p>Hello, how are you</p>"
 *    }
 */
export const getResolvedFieldMetadata = (propertyReference: string, context: string) => {
  const pageRef = propertyReference.split('.');
  const propName = pageRef.pop();

  const c11nEnv = new C11nEnv({
    options: {
      pageReference: `${pageRef.join('.')}`,
      context
    }
  });

  const fieldMetadata = getPropertyMetadata(propName, c11nEnv.getCurrentClassID());
  const resolvedFieldMetadata = {};
  c11nEnv._classID = c11nEnv.getCurrentClassID();
  c11nEnv.resolveConfigProps(fieldMetadata, resolvedFieldMetadata);
  return resolvedFieldMetadata;
};
