/**
 * This functions returns context for given target container and key
 * @function getContextByTargetandKey
 * @param target -- target container name e.g. app/primary
 * @param key -- key within container item to search for e.g. OO6GFG-TH1COS-WORK T-157004
 * @private
 *
 * @example <caption>Example for getContextByTargetandKey.</caption>
 * PCore.getContainerUtils().getContextByTargetandKey('app/primary', OO6GFG-TH1COS-WORK T-157004);
 * example is for returning context present under 'app/primary_2/workarea => item => app/primary_2/workarea_1 => context'
 */
export const getContextByTargetAndKey = (target: string, key: string) => {
  const containerItemID = getContainerItemName(target, key);
  const workareaContainerItemId = getContainerItemName(`${containerItemID}/workarea`, key);
  return getDataContextName(workareaContainerItemId);
};

/**
 * This function returns the parentContext value for given container item if it exists
 * @private
 * @function getContainerParentContext
 * @param containerItemName -- containeritem name e.g. app/primary_4
 * @returns - 1) parent context if exists in container item
 *          - 2) null if no data found
 *
 * @example <caption>Example for getContainerParentContext.</caption>
 * PCore.getContainerUtils().getContainerParentContext('app/primary_3/modal_1');
 * example is for returning data context name for container item name --> 'app/primary_3'
 *  */
export const getContainerParentContext = (containerItemName: string): string | null => {
  if (!isNonEmptyString(containerItemName)) {
    return null;
  }
  return getValueFromContainerItem(containerItemName, 'parentContext');
};

export const getActiveContainerItemIDFromRoutingInfo = (routingInfo: any) => {
  const { accessedOrder } = routingInfo;

  if (accessedOrder && accessedOrder.length > 0) {
    return accessedOrder[accessedOrder.length - 1];
  }
  return '';
};

/**
 * This function returns the parentContainerItem given the containerItemID.
 * @private
 * @param containerItemId
 * @returns string
 * @example <caption>Example for getContainerParentContext.</caption>
 * PCore.getContainerUtils().getParentContainerItem('app/primary_3/modal_1');
 * example is for returning data context name for container item name --> 'app/primary_3'
 */
export const getParentContainerItem = (containerItemId: string | null): string | null => {
  if (containerItemId && typeof containerItemId === 'string') {
    const splitArr = containerItemId.split('/');
    splitArr.pop();
    return splitArr.join('/');
  }
  return null;
};

export const getFlowContainer = () => FlowContainer;
