/**
 * Sends a message in an existing conversation with the GenAI Assistant.
 * @function
 * @static
 * @example <caption>In this example, the API sends a message in an existing conversation with a GenAI Assistant.</caption>
 * PCore.getGenAIAssistantUtils().sendMessage('UPlus-SAPlusC11n-Work-Opportunity-Biz!OpportunityAssistant','CON-38008','test message','app/primary_1',{enableStreaming:true})
 * .then(response => {
 *    // response
 * }).catch(err => {
 *   // errors
 * });
 * @param   {string}  assistantID     The unique identifier of the GenAI Assistant with which the conversation is occurring. Note: Ensure that you provide the pxInsName value of the GenAI assistant.
 * @param   {string}  conversationID  The unique identifier of the object that contains the details of the conversation.
 * @param   {string}  message         The message that must be sent to the assistant.
 * @param   {string}  context         The name of the context where the API is being called.
 * @param   {SendMessageConfig} config The object containing additional configuration options for the message sent to the Pega GenAI Coach.
 * @param   {boolean} config.enableStreaming The flag that determines if the response to the message sent to the Pega GenAI Coach uses server-sent events. Default value is false, if enableStreaming is true, the response from the GenAI Coach is a ReadableStream which means the response is displayed sequentially. If enableStreaming is false, the response from the GenAI Coach is a JSON object.
 * @returns {Promise<object>}         A Promise that resolves to an object containing the response from the GenAI Assistant.
 */
const sendMessage = (
  assistantID: string,
  conversationID: string,
  message: string,
  context: string,
  config?: SendMessageConfig
) => {
  return processSendMessageRequest(
    SEND_MESSAGE,
    assistantID,
    conversationID,
    message,
    context,
    undefined,
    config?.enableStreaming
  );
};

/**
 * Sends a message in an existing conversation with an Agent.
 * @function
 * @static
 * @example <caption>In this example, the API sends a message in an existing conversation with an Agent.</caption>
 * PCore.getGenAIAssistantUtils().sendMessageForAgent(
 * 'Work-Opportunity-Biz!OpportunityAgent',
 * 'CON-38008',
 * 'test message',
 * 'app/primary_1',
 * [{
 *    category: 'File',
 *    ID: 'b7edfb75-5a9f-4cf3-b282-22e16e9deadf'
 *  }],
 * {enableStreaming:true}
 * ).then(response => {
 *    // response
 * }).catch(err => {
 *   // errors
 * });
 * @param   {string}  agentID         The unique identifier of the Agent with which the conversation is occurring. Note: Ensure that you provide the pxInsName value of the agent.
 * @param   {string}  conversationID  The unique identifier of the object that contains the details of the conversation.
 * @param   {string}  message         The message that must be sent to the agent.
 * @param   {string}  context         The name of the context where the API is being called.
 * @param   {Attachment[]} attachments The list of files uploaded to the server that must be sent along with the message.
 *                                    Note: For more information on uploading a file to the server, see uploadAttachment from getAttachmentUtils module
 * @param   {SendMessageConfig} config The object containing additional configuration options for the message sent to the Agent.
 * @param   {boolean} config.enableStreaming The flag that determines if the response to the message sent to the Agent uses server-sent events. Default value is false, if enableStreaming is true, the response from the Agent is a ReadableStream which means the response is displayed sequentially. If enableStreaming is false, the response from the Agent is a JSON object.
 * @returns {Promise<object>}         A Promise that resolves to an object containing the response from the Agent.
 */
const sendMessageForAgent = (
  agentID: string,
  conversationID: string,
  message: string,
  context: string,
  attachments?: Attachment[],
  config?: SendMessageConfig
) => {
  return processSendMessageRequest(
    SEND_MESSAGE_FOR_AGENT,
    agentID,
    conversationID,
    message,
    context,
    attachments,
    config?.enableStreaming
  );
};

/**
 * Likes a message in a conversation with an Agent.
 * @function
 * @static
 * @example <caption>In this example, the API likes a message with ID M-100 in a conversation with an Agent.</caption>
 * PCore.getGenAIAssistantUtils().likeAgentMessage('Work-Opportunity-Biz!OpportunityAgent','CON-38008','M-100','app/primary_1')
 * .then(response => {
 *    // response
 * }).catch(err => {
 *   // errors
 * });
 * @param   {string}  agentID         The unique identifier of the Agent with which the conversation is occurring. Note: Ensure that you provide the pxInsName value of the agent.
 * @param   {string}  conversationID  The unique identifier of the object that contains the details of the conversation.
 * @param   {string}  messageID       The unique identifier of the message that is being liked.
 * @param   {string}  context         The name of the context where the API is being called.
 * @returns {Promise<object>}         A Promise that resolves to an object containing the response from the Agent.
 */
const likeAgentMessage = (agentID: string, conversationID: string, messageID: string, context: string) => {
  const requestPayload: {
    queryPayload: { assistantID?: string; agentID?: string; conversationID: string; messageID: string };
    body?: string;
  } = {
    queryPayload: { agentID, conversationID, messageID }
  };

  return invokeRestApi(LIKE_AGENT_MESSAGE, requestPayload, context);
};

/**
 * Unlikes a message in a conversation with an Agent.
 * @function
 * @static
 * @example <caption>In this example, the API unlikes a message with ID M-100 in a conversation with an Agent.</caption>
 * PCore.getGenAIAssistantUtils().unLikeAgentMessage('Work-Opportunity-Biz!OpportunityAgent','CON-38008','M-100','app/primary_1','feedback text')
 * .then(response => {
 *    // response
 * }).catch(err => {
 *   // errors
 * });
 * @param   {string}  agentID         The unique identifier of the Agent with which the conversation is occurring. Note: Ensure that you provide the pxInsName value of the agent.
 * @param   {string}  conversationID  The unique identifier of the object that contains the details of the conversation.
 * @param   {string}  messageID       The unique identifier of the message that is being disliked.
 * @param   {string}  context         The name of the context where the API is being called.
 * @param   {string}  feedbackText    The feedback provided for the unliked message. Note: The default value is empty string.
 * @returns {Promise<object>}         A Promise that resolves to an object containing the response from the Agent.
 */
const unLikeAgentMessage = (
  agentID: string,
  conversationID: string,
  messageID: string,
  context: string,
  feedbackText = ''
) => {
  const requestPayload = {
    queryPayload: { agentID, conversationID, messageID },
    body: JSON.stringify({ feedbackText })
  };

  return invokeRestApi(UNLIKE_AGENT_MESSAGE, requestPayload, context);
};

export default {
  createConversation,
  sendMessage,
  createConversationForAgent,
  sendMessageForAgent,
  likeAgentMessage,
  unLikeAgentMessage,
  getConversations,
  getConversationsForAgent,
  getMessages,
  getMessagesForAgent
};
