  /**
   * Checks and resolves any context related info.
   * like, any errors for properties and any validation
   * info for this component.
   * @param target target object to update the context.
   * @private
   */
  resolveContextProps(target: { error: string; validatemessage: string }) {
    const { value } = this.getStateProps();
    if (value) {
      target.error = this.getContextDataValue(`${this.getPageReference() + value}.error`);
      target.validatemessage = this.getContextDataValue(`${this.getPageReference() + value}.validatemessage`);
    }
  }

