import * as actions from '../../store/state/actions/action-creators';
import { CONTAINER_NAMES, getDataContextName } from '../../container/container-utils';
import { getParentContextName } from '../../utils/context-utils';
import { getContainerNameFromContainerItemID } from '../common-helper';
import type { Action } from '../../store/state/state-manager';
import type { DxAPIResponseObject } from '../types';
import type { Actionout } from '../../types';

export const getContainerName = (actionInPayload: Action['payload']) => {
  const { isModalAction, containerItemID } = actionInPayload;
  return isModalAction ? CONTAINER_NAMES.MODAL : getContainerNameFromContainerItemID(containerItemID);
};

export const addActionToUpdateParentContextCaseViewMode = async (
  actionsOut: Actionout[],
  options: {
    childContainerItemID: string;
    caseViewMode: string;
    referencedUsers: object | object[];
    response: DxAPIResponseObject;
  }
) => {
  const { childContainerItemID, caseViewMode, referencedUsers, response } = options;
  const parentContainerItemID = getParentContextName(childContainerItemID);
  const parentContextName = getDataContextName(parentContainerItemID);

  const actionPayload = {
    context: parentContextName,
    data: {
      ...response.data,
      referencedUsers,
      context_data: {
        caseViewMode
      }
    }
  };

  if (response?.data?.caseInfo) {
    response.data.caseInfo.assignments ??= [];
  }

  actionsOut.push(actions.postProcess(actions.mergeData, actionPayload));
};
