import type { AxiosResponse } from 'axios';

import { resolveRestApi } from '../router/app-router';
import { fetch as axiosFetch } from '../service_broker/fetch';
import constants from '../constants';
import CaseUtils from '../case/utils';
import { getChildContainers } from '../container/container-utils';
import AssetLoader from '../utils/asset-loader';
import EnvironmentInfo from '../environment-info';
import type { Parameters } from '../datapage/types';
import appRouteParser from '../router/route-parser';

import type { DataOptions, DataResponse, ChangeSet, MoveListRecordsPayload, Payload } from './types';

const GET_LIST_DATA = 'getListData';
const GET_DATA_VIEW_METADATA = 'getDataViewMetadata';
const GET_LIST_DATA_COUNT = 'getListDataCount';
const POST_DATA_VIEW_METADATA = 'postDataViewMetadata';
const MOVE_LIST_RECORDS = 'moveListRecords';
const ACTION_GET_DATA_OBJECT_VIEW = 'readDataObject';

/**
 * Note: The terms data page and data view used in context of this API are interchangeable.
 * This function helps in fetching the data of a list type data view.
 * @ignore
 * @param dataViewName - name of the data view.
 * @param payload - query object containing information about the list of columns, filters and pagination.
 * @param context - context name eq., primary
 * @param options - object containing extra information like skipClearErrorMessages to not clear the error messages
 */
export const getListDataView = (
  dataViewName: string,
  payload?: Payload,
  context?: string,
  options: DataOptions = {}
): Promise<DataResponse> => {
  const reqPayload = {
    dataViewName
  };
  const { skipClearErrorMessages, signal, doNotMergeMessagesForStatusCode } = options;
  const { restApi, reqType } = resolveRestApi(GET_LIST_DATA, reqPayload);
  return axiosFetch(
    restApi,
    {
      method: reqType,
      body: payload,
      ...(signal && { signal }),
      ...(skipClearErrorMessages && { skipClearErrorMessages })
    },
    context,
    doNotMergeMessagesForStatusCode
  );
};

/**
 * Note: The terms data page and data view used in context of this API are interchangeable.
 * This function helps in fetching the view metadata of an object initialized through a data page.
 * @param dataViewName - The name of the view that contains the data of the object
 * @param viewName - name of the view.
 * @param dataViewParams - object that will set parameters to the data view
 * @param context - context name eq., primary
 * @param options - object containing extra information like skipClearErrorMessages to not clear the error messages
 *
 * *
 * @example  <caption>In the example below, the API retrieves the the Glimpse of an operator.</caption>
 *
 * const dataViewName = "D_pyOperatorGlimpse";
 * const viewName = "pyGlimpse";
 * const dataViewParams = { opname: "joe" };
 * const context = "app/primary_1";
 * PCore.getDataApiUtils().getDataObjectView(dataViewName, viewName, dataViewParams, context)
 * .then(response => {
 *  //The response of this API is as shown below:
 *  {
 *    fetchDateTime: "2020-06-29T11:06:23.896Z"
 *    data: { uiResources: { ... }, data: { ... } }
 *  }
 * })
 * .catch(error => {
 *  console.log(error);
 * });
 *
 * @returns response object
 */
export const getDataObjectView = (
  dataViewName: string,
  viewName: string,
  dataViewParams: Parameters,
  context: string,
  options: DataOptions = {}
): Promise<AxiosResponse<object>> => {
  const { skipClearErrorMessages, signal, doNotMergeMessagesForStatusCode } = options;
  const queryPayload = {
    data_view_ID: dataViewName,
    view_ID: viewName
  };
  const reqPayload = { dataViewParameters: dataViewParams };
  const { restApi, reqType } = resolveRestApi(ACTION_GET_DATA_OBJECT_VIEW, queryPayload);
  return axiosFetch(
    restApi,
    {
      method: reqType,
      body: reqPayload,
      ...(signal && { signal }),
      ...(skipClearErrorMessages && { skipClearErrorMessages })
    },
    context,
    doNotMergeMessagesForStatusCode
  );
};

/**
 * Note: The terms data page and data view used in context of this API are interchangeable.
 * This API obtains the number of records in a data view as a Promise.
 * @param dataViewName - The name of the data view that contains the records whose count must be obtained.
 * @param payload - A query object containing the details of list of columns, filter conditions, and pagination to be retrieved.
 * @param context - The name of the context where the API is being called.
 *
 * @example  <caption>In the example below, the API retrieves the number of records of employees whose gender is 'Female' and whose role is 'Finance'.</caption>
 *
 * const dataViewName = "D_EmployeeList";
 * const payLoad = {
 *  "dataViewParameters": {
 *    "dept": "HR"
 *  },
 * "query": {
 *  "distinctResultsOnly": true,
 *   "filter": {
 *     "filterConditions": {
 *       "F1": {
 *         "comparator": "EQ",
 *         "ignoreCase": true,
 *         "lhs": {
 *           "field": "Role"
 *         },
 *         "rhs": {
 *           "value": "Finance"
 *         }
 *       },
 *      "F2": {
 *         "comparator": "EQ",
 *         "ignoreCase": true,
 *         "lhs": {
 *           "field": "Gender"
 *         },
 *         "rhs": {
 *           "value": "Female"
 *         }
 *       }
 *     },
 *     "logic": "F1 AND F2"
 *   }
 *   "select": [
 *     {
 *       "field": "pyID"
 *     }
 *   ]
 *  }
 * };
 * const context = "app/primary_1";
 * PCore.getDataApiUtils().getListCount(dataViewName, payload, context)
 * .then(response => {
 *  //The response of this API is as shown below:
 *  {
 *    fetchDateTime: "2020-06-29T11:06:23.896Z"
 *    hasMoreResults: false
 *    resultCount: 4923
 *  }
 * })
 * .catch(error => {
 *  console.log(error);
 * });
 *
 *
 * @returns response object
 */

export const getListCount = (
  dataViewName: string,
  payload?: Payload,
  context?: string
): Promise<AxiosResponse<object>> => {
  const reqPayload = {
    dataViewName
  };
  const { restApi, reqType } = resolveRestApi(GET_LIST_DATA_COUNT, reqPayload);
  return axiosFetch(
    restApi,
    {
      method: reqType,
      body: payload
    },
    context
  );
};

/**
 * Note: The terms data page and data view used in context of this API are interchangeable.
 * This API retrieves the list of data records in a data view as a Promise.
 * @param dataViewName - The name of the data view from which the list of data records must be retrieved.
 * @param payload A query object containing the details of list of columns, filter conditions, and pagination to be retrieved.
 * @param context - The name of the context where the API is being called.
 * @param options - object containing extra information like skipClearErrorMessages
 * @example  <caption>In the example below, the API retrieves the first 10 records of employees whose gender is 'Male' and whose role is 'Software'.</caption>
 *
 * const dataViewName = "D_EmployeeList";
 * const payLoad = {
 * "dataViewParameters": {
    "dept": "Engineering"
  },
  "query": {
    "distinctResultsOnly": true,
    "filter": {
      "filterConditions": {
        "F1": {
          "comparator": "EQ",
          "ignoreCase": true,
          "lhs": {
            "field": "Role"
          },
          "rhs": {
            "value": "Software"
          }
        },
        "F2": {
          "comparator": "EQ",
          "ignoreCase": true,
          "lhs": {
            "field": "Gender"
          },
          "rhs": {
            "value": "Male"
          }
        }
      },
      "logic": "F1 AND F2"
    }
    "select": [
      {
        "field": "Name"
      },
      {
        "field": "Role"
      },
      {
        "field": "Gender"
      }
    ]
  },
  "paging":{
    "pageNumber":1,
    "pageSize":10
  }
};
 * const context = "app/primary_1";
 * PCore.getDataApiUtils().getData(dataViewName, payload, context)
 * .then(response => {
 *  // The response of this API is as shown below:
 *  {
 *      data: [
 *        {
 *          "Name" : "Mark wood",
 *          "Role" : "Software",
 *          "Gender" "Male"
 *        },
 *        {
 *          "Name" : "Edwards",
 *          "Role" : "Software",
 *          "Gender" "Male"
 *        }
 *      ]
 *      fetchDateTime: "2020-06-29T11:06:24.329Z"
 *      pageNumber: 1
 *      pageSize: 10
 *  }
 * })
 * .catch(error => {
 *    console.log(error);
 * });
 *
 *
 * @returns response object
 */
export const getData = (
  dataViewName: string,
  payload?: Payload,
  context?: string,
  options?: DataOptions
): Promise<DataResponse> => {
  return getListDataView(dataViewName, payload, context, options);
};

/**
 * Note: The terms data page and data view used in context of this API are interchangeable.
 * This API obtains the metadata of a data view as a Promise.
 * @param dataViewName - The name of the data view whose metadata must be obtained.
 * @param context - context name eq., primary
 * @param associationFilter - List of simple/complex associations to fetch the fields from
 * @param propertyFilter - List of field IDs to which the response should be limited to
 *
 * @example  <caption>In the example below, the API returns the metadata of the data view whose name is 'D_BugList'.</caption>
 *
 * const dataViewName = "D_BugList"
 * PCore.getDataApiUtils().getDataViewMetadata(dataViewName);
 *
 * //The response of this api will be like shown below.
 * {
  "classID": "PegaProjMgm-Work-Bug",
  "className": "Bug",
  "structure": "List",
  "isQueryable": true,
  "fields": [
    {
      "description": "operator who manager assigns work to",
      "fieldID": "pyAssignedOperator",
      "fieldType": "Identifier",
      "isReadOnly": false,
      "name": "Assigned To",
      "dataType": "Identifier"
    },
    {
      "description": "This property is used to identify the work object's parent
      Backlog and should include the pyID of that work object. In the future,
      it will be derived from the user's data input into UserStoryIDEntry or
      based upon the context of the creation of the item.",
      "displayAs": "pxTextInput",
      "fieldID": "BacklogID",
      "fieldType": "Text (single line)",
      "isReadOnly": false,
      "name": "Backlog ID",
      "maxLength": 32,
      "dataType": "Text"
    }
  ]
}
 */
export const getDataViewMetadata = (
  dataViewName: string,
  context: string | undefined,
  associationFilter?: string[] | null,
  propertyFilter?: string[]
) => {
  const hasAssociationFilter = !!associationFilter;
  const hasPropertyFilter = !!propertyFilter;
  const routeForPostDataViewMetadataExists = appRouteParser.doesRestApiExist(POST_DATA_VIEW_METADATA);
  const routeKey =
    (hasAssociationFilter || hasPropertyFilter) && routeForPostDataViewMetadataExists
      ? POST_DATA_VIEW_METADATA
      : GET_DATA_VIEW_METADATA;

  const reqPayload = { dataViewName };
  const { restApi, reqType } = resolveRestApi(routeKey, reqPayload);

  if (routeForPostDataViewMetadataExists) {
    if (hasAssociationFilter) {
      return axiosFetch(restApi, { method: reqType, body: { associationFilter } }, context);
    }
    if (hasPropertyFilter) {
      return axiosFetch(restApi, { method: reqType, body: { propertyFilter } }, context);
    }
  }
  return axiosFetch(restApi, { method: reqType }, context);
};

