import type { Store, Dispatch } from 'redux';

import constants, { publicConstants } from '../../../constants';
import PubSubUtils from '../../../utils/pubsub-utils';
import Utils from '../../../utils/utils';
import { REMOVE_CONTAINER_ITEM, REPLACE_CONTAINER_ITEM } from '../actions';
import GarbageCollector from '../../../garbage-collector/index';

/**
 * Redux middleware to track the redux changes
 * and publish pub-sub events
 * @param store the store that will be affected
 */

const PubSubMiddleware =
  (store: Store) =>
  (next: Dispatch) =>
  (action: {
    type: string;
    payload?: {
      target: string;
      reference?: string;
      containerItemID: string;
      context: string;
      childContainersContexts?: object[];
      containerInfo?: { newAccessedOrder: string[]; containerType: string };
      actionPerformed?: string;
    };
  }) => {
    const {
      type,
      payload: {
        target,
        containerItemID,
        context: dataContext,
        childContainersContexts = [],
        containerInfo: { newAccessedOrder, containerType } = {} as {
          newAccessedOrder: string[];
          containerType: string;
        }
      } = {}
    } = action;
    const { PUB_SUB_EVENTS } = publicConstants;

    if (type === REMOVE_CONTAINER_ITEM) {
      const { resourceType, key, context } =
        Utils.getIn(store.getState(), ['containers', target, 'items', containerItemID]) || {};
      const eventPayload = { resourceType, key, context, containerItemID };
      PubSubUtils.publish(PUB_SUB_EVENTS.CONTAINER_EVENTS.CLOSE_CONTAINER_ITEM, eventPayload);
      if (
        target === `${publicConstants.APP.APP}/${constants.PRIMARY}` &&
        containerType === constants.CONTAINER_TYPE.MULTIPLE
      ) {
        PubSubUtils.publish(PUB_SUB_EVENTS.CONTAINER_EVENTS.CLOSE_MULTI_TYPE_PRIMARY_CONTAINER_ITEM, {
          key,
          newAccessedOrder
        });
      }
    }

    if (
      childContainersContexts &&
      childContainersContexts.length > 0 &&
      (type === REPLACE_CONTAINER_ITEM || type === REMOVE_CONTAINER_ITEM)
    ) {
      childContainersContexts.forEach((childContext: any) => {
        GarbageCollector.cleanCaches(childContext);
      });
    }

    if (type === REPLACE_CONTAINER_ITEM || type === REMOVE_CONTAINER_ITEM) {
      GarbageCollector.cleanCaches(dataContext as string);
    }
    next(action);
  };

export default PubSubMiddleware;
