export const updateContainerItem = (payload: Action['payload']) => {
  const { containerItemID } = payload;
  payload.suggestionContexts = getSuggestionContexts(containerItemID);

  // propertiesToReplace is not needed when the caseInfo content is empty as the there are no properties to be replaced
  const caseContent = payload?.data?.caseInfo?.content;
  const hasCaseContent = Object.keys(caseContent || {}).length > 0;

  payload.propertiesToReplace = hasCaseContent ? CaseUtils.getPropertiesToReplace(containerItemID) : [];

  return {
    type: action.UPDATE_CONTAINER_ITEM,
    payload
  };
};

