/* Data Actions Section */

/**
 * returns the next action object
 * @ignore
 * @param nextAction next action creator function.
 * @param restArguments rest arguments
 * @returns next action object.
 */
export const postProcess = (nextAction: any, ...restArguments: any) => {
  ActionProcessor.resume();
  return nextAction(...restArguments);
};

/**
 * @ignore
 */
export type ActionError = {
  payload: { msg: string; err: any; context?: string; formFields: any; actionMgrID?: string };
  type: string;
};

/**
 * returns the error action object
 * @ignore
 * @param msg message for the error.
 * @param err actual error object
 * @param context object.
 * @param formFields actual formFields.
 * @returns the action object.
 */
export const error = (msg: string, err: any, context?: string, formFields?: any, actionMgrID?: string): ActionError => {
  return {
    type: action.ERROR,
    payload: {
      msg,
      err,
      context,
      formFields,
      actionMgrID
    }
  };
};

