  /**
   * This API performs the resolve duplicate action for the search duplicate cases automation step configured in a case.
   * <br>
   * @example <caption>Example for resolveDuplicateCase()</caption>
   * Example usage - In this example, the API performs the resolve duplicate action for the search duplicate cases automation
   * step configured in the app/primary_1/workarea_1 container.
   * const resolveDuplicateCasePromise = getPConnect().getActionsApi().resolveDuplicateCase("app/primary_1/workarea_1"); <br>
   * resolveDuplicateCasePromise.then(() => {
   *  // resolve duplicate case success handling
   * }).catch(() => {
   *  // resolve duplicate case failure handling
   * });
   *
   * // The success callback is called if the resolve duplicate case action is successful.
   *
   * @param containerItemID - The ID of the container item that provides the current context of the case <br>
   * For example: "app/primary_1/workarea_1"
   *
   * @returns A promise associated with the action (and stored in ActionManager)
   */
  resolveDuplicateCase(containerItemID: string) {
    if (!containerItemID) {
      return Promise.reject(new Error(this.MSG_CONTAINERITEMID_REQUIRED));
    }

    if (typeof containerItemID !== 'string') {
      return Promise.reject(new TypeError(this.MSG_CONTAINERITEMID_STRING));
    }

    const [, duplicateCaseID] = this.c11nEnv.getValue(publicConstants.CASE_INFO.DUPLICATECASEINSKEY).split(' ');
    const actionSetDuplicateCaseFlag = {
      type: SET_PROPERTY,
      payload: {
        reference: publicConstants.CASE_INFO.DUPLICATECASEFLAG,
        value: Constants.DUPLICATE,
        context: containerItemID
      }
    };
    const actionSetDuplicateCaseID = {
      type: SET_PROPERTY,
      payload: {
        reference: publicConstants.CASE_INFO.DUPLICATECASEID,
        value: duplicateCaseID,
        context: containerItemID
      }
    };
    StateManager.dispatch([actionSetDuplicateCaseFlag, actionSetDuplicateCaseID]);
    return this.finishAssignment(containerItemID);
  }

