  /**
   *
   * Displays the data of a page based on the data context.
   *
   * @example <caption>Example for showData()</caption>
   * Example usage -
   * const showDataPromise = getPConnect().getActionsApi().showData("EmployeeDetails", "D_EmployeeDetails", {pyGUID: "0759409f-4146-439c-aa25-57d4f495fee5"}); <br>
   * showDataPromise.then(() => {
   *   // show data success handling
   * }).cath(() => {
   *   // show data failure handling
   * });
   *
   *
   * @param pageName - The name of the view in which the data must be displayed.
   * @param dataContext - The name of page type data page whose data must be displayed.
   * @param dataContextParameters - The parameters associated with the data page.
   * @param options - The JavaScript object containing the properties to display the data.<br>
   * @param options.containerName - The name of the container that displays the data.
   * @param options.skipSemanticUrl - The flag that determines if the semantic URL evaluation logic must be skipped.
   * <br>
   * * Note:
   * - The default value is false
   * - If the value is false, the semantic URL evaluation logic will not be skipped.
   * - If the value is true, the semantic URL evaluation logic will be skipped.
   *
   * <br><br>
   * @returns A promise associated with the action (and stored in ActionManager)
   */
  showData(
    pageName: string,
    dataContext: string,
    dataContextParameters: object,
    options: {
      container?: string;
      context?: string;
      containerName?: string;
      skipSemanticUrl?: boolean;
    } = {
      containerName: '',
      skipSemanticUrl: false
    }
  ) {
    if (!pageName) {
      return Promise.reject(new Error(this.MSG_PAGENAME_REQUIRED));
    }

    if (typeof pageName !== 'string') {
      return Promise.reject(new TypeError(this.MSG_PAGENAME_STRING));
    }

    if (!dataContext) {
      return Promise.reject(new Error('dataContext is required parameter'));
    }

    if (typeof dataContext !== 'string') {
      return Promise.reject(new TypeError('dataContext must be string'));
    }

    const actionConfig = ActionProcessor.buildActionObject(
      {
        pageName,
        dataContext,
        dataContextParameters,
        RemoteSystemID: RemoteCase.getRemoteHeaderByContext(this.c11nEnv.getContextName())
      },
      this.c11nEnv
    );
    const theType = actionType.SHOW_DATA;

    if (!options?.containerName) {
      options.containerName =
        EnvironmentInfo.getRenderingMode() === 'HYBRID'
          ? RuntimeParamsAPI.getRuntimeParam(Constants.HYRBID_ROOT_CONTAINERNAME)
          : TargetMap.getTargetViewContainerName(theType);
    }

    // register this action with the ActionManager using
    //  actionMgrID that's been included in the action's payload
    //  and a promise. Note that buildActionObject gets an ActionMgrID
    //  and puts it in the returned object
    const { promise } = this.actionsApiPromise(theType, actionConfig);

    StateManager.dispatch({
      type: theType,
      payload: { ...actionConfig, ...options, skipSemanticUrl: Boolean(options.skipSemanticUrl) }
    });

    // return the Promise associated with this action
    return promise;
  }

