import PubSubUtils from '../utils/pubsub-utils';

import { fetch, waitForUpdateSession } from './fetch';
import { getJWTServerDetails, isUpdateSessionRequest } from './utils';

export const reissueWebToken = (errorObj: any, fetchStatus: any, authInfo: any) => {
  const { config } = errorObj;
  const { url, config: configObj, callback, isRefreshTokenPresent } = getJWTServerDetails(authInfo);
  const context = config.headers && config.headers.context;
  if (config.data) {
    config.body = config.data;
  }

  const retryRequestPromise = waitForUpdateSession(config.url, config, context, fetchStatus);
  if (!isRefreshTokenPresent) {
    callback?.failure?.(config.url, authInfo);
    return retryRequestPromise;
  }

  return fetch(url, configObj)
    .then((resp: any) => {
      fetchStatus.setUpdateSessionStatus(false);
      callback?.success?.(resp, config);
      PubSubUtils.publish('updateSession', { updateStatus: 'success' });
      PubSubUtils.publish('OnUpdateSession', {});
      return retryRequestPromise
        .then((response) => {
          return Promise.resolve(response);
        })
        .catch((error) => {
          return Promise.reject(error);
        });
    })
    .catch((errorResponse: any) => {
      if (errorResponse && errorResponse.config && isUpdateSessionRequest(errorResponse.config.url, authInfo)) {
        callback?.failure?.(config.url, authInfo);
        return retryRequestPromise;
      }
      return Promise.reject(errorResponse);
    });
};

export default reissueWebToken;
