import { ofType } from 'redux-observable';
import { map, mergeMap } from 'rxjs/operators';
import { Observable, of } from 'rxjs';

import APP_CONSTANTS from '../../constants';
import * as actions from '../../store/state/actions/action-creators';
import * as StateManager from '../../store/state/state-manager';
import { processDataToDispatchErrorAction } from '../../store/state/epics/util';
import { isErrorActionType, promiseHandler } from '../../case/common-helper';
import type { Action } from '../../store/state/state-manager';
import type { Actionout } from '../../types';
import * as ContainerUtils from '../../container/container-utils';
import { localeUtils } from '../../locale/locale-utils';
import { publishDisplayCreatedCaseToasterEvent } from '../../case/create-work/events';

import { DELETE_WORK } from './action-creator';
import { executeDeleteWorkRequest } from './async-request';
import { publishWorkDeletedEvent } from './events';

const deleteWork = (action$: Observable<Action>) => {
  let actionMgrID = 0;
  let caseTypeID: string;
  let actionsOut: Actionout[] = [];

  return action$.pipe(
    ofType(DELETE_WORK),
    map((action: Action) => {
      ({ actionMgrID, caseTypeID } = action.payload);
      return action.payload;
    }),
    mergeMap((actionInPayload) => {
      return actionInPayload.ignoreWorkDeletion ? of({ actionInPayload }) : executeDeleteWorkRequest(actionInPayload);
    }),
    mergeMap(async (data) => {
      if (data && isErrorActionType(data.type)) {
        const { payload } = data;
        const errorAction = processDataToDispatchErrorAction({ ...data, ...payload, actionMgrID });
        actionsOut.push(errorAction);
        return actionsOut;
      }

      const { actionInPayload } = data;
      const { context } = actionInPayload;

      publishDisplayCreatedCaseToasterEvent({
        content: localeUtils.getLocaleValue('Deleted successfully!', 'Messages')
      });

      // For preview scenario, we need to close the preview panel
      if (context?.includes(APP_CONSTANTS.PREVIEW)) {
        actionsOut.push(
          actions.postProcess(
            actions.resetContainers,
            ContainerUtils.resetContainersPayload({
              context: APP_CONSTANTS.APP.APP,
              name: APP_CONSTANTS.PREVIEW
            })
          )
        );
      }

      // For case page scenario, we need to return to the previous screen
      if (context?.includes(APP_CONSTANTS.PRIMARY)) {
        window?.history?.back();
      }

      return actionsOut;
    }),
    map(() => {
      StateManager.dispatch(actionsOut);
      if (promiseHandler(actionsOut, actionMgrID) === APP_CONSTANTS.PROMISE_VALUE.RESOLVE) {
        publishWorkDeletedEvent({ caseTypeID });
      }
      actionsOut = [];
      return actionsOut;
    })
  );
};

export default deleteWork;
