import {
  addActionsToParentContext,
  getActionPayload,
  processHeaders,
  processViewResponse,
  SCOPE_TYPES,
  getDirtyDetails
} from '../store/state/epics/util';
import {
  ACTION_OPEN_PROCESS_ACTION,
  ACTION_OPEN_CASE_WIDE_LOCAL_ACTION,
  ACTION_OPEN_LOCAL_ACTION,
  ACTION_OPEN_BULK_ACTION
} from '../store/state/actions';
import { ActionManagerInstance } from '../actions/manager';
import { dispatch } from '../store/state/state-manager';
import { getResolvedTargetName } from '../router/routing-manager';
import { buildContextName, buildModalContext } from '../utils/context-utils';
